* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family:
        system-ui,
        -apple-system,
        BlinkMacSystemFont,
        "Segoe UI",
        Roboto,
        Oxygen,
        Ubuntu,
        Cantarell,
        "Open Sans",
        "Helvetica Neue",
        sans-serif;
}

/* basic layout */

body {
    min-height: 100svh;
    background-color: rgb(120, 182, 252);
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: contain;
    background-repeat: round;
    backdrop-filter: blur(60px);
}

.tile {
    width: 45px;
    height: 45px;
    border: 1px solid rgba(255, 255, 255, 0.589);
    margin: 1px;
    background-color: rgba(93, 153, 194, 0.116);
}

.active {
    border: 1px solid rgba(255, 255, 255, 0.925);
    margin-top: 2px;
}

.letterTile,
.numberTile {
    display: flex;
    width: 45px;
    height: 45px;
    align-items: center;
    justify-content: end;
    font-size: 1.25rem;
    font-weight: 600;
}

.numberTile {
    font-size: 1.4rem;
    align-items: end;
    justify-content: center;
}

.letterTile {
    max-width: fit-content;
    margin-right: 8px;
}

.numRow {
    display: flex;
    margin-left: 8px;
}

.row {
    display: flex;
}

.ship {
    background-color: rgb(175, 175, 175);
}

.hideShip {
    background-color: rgba(97, 137, 224, 0);
}

.miss {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-repeat: no-repeat;
    background-size: 85%;
    background-position: center;
}

.hit {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    background-repeat: no-repeat;
    background-size: 85%;
    background-position: center;
}

.hitLogMsg {
    color: rgb(255, 0, 0);
}
/* start screen ui layout */

body #startScreen {
    display: flex;
    flex-direction: column;
}

.flexRow {
    display: flex;
    justify-content: center;
    align-items: center;
}

h1 {
    text-align: center;
}

input {
    width: 200px;
    height: 37px;
    margin-top: 16px;
    padding: 7px;
    font-size: 1rem;
    border-radius: 999px;
    border: none;
    outline: none;
    padding-left: 12px;
    color: white;
    font-weight: 700;
    background-color: rgba(0, 0, 0, 0.185);
}

input::placeholder {
    color: rgba(255, 255, 255, 0.555);
}

.controls {
    align-self: center;
    display: flex;
    flex-direction: column;
}

#player1Section,
#player2Section {
    display: flex;
}

.board {
    display: flex;
    flex-direction: column;
    align-items: end;
    width: 495px;
    margin: 0 2rem;
}

.boardContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#log {
    width: 200px;
    display: flex;
    flex-direction: column;
    gap: 32px;
    margin-top: 4rem;
    overflow: hidden;
}

#player1Log,
#player2Log {
    font-size: 2.5rem;
    font-weight: 500;
    text-align: center;
}

#buttonFooter {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    gap: 16px;
}

#buttonFooter > span {
    font-size: 2rem;
    font-weight: 700;
}

.name {
    font-size: 2rem;
    font-weight: bold;
    margin-top: 16px;
}

footer {
    text-align: center;
    border-top: rgb(0, 0, 0) 2px solid;
    border-bottom: rgb(0, 0, 0) 2px solid;
    margin-top: 2rem;
    font-size: 3.25rem;
    padding: 8px;
    font-weight: 500;
    background-color: rgba(0, 0, 0, 0.308);
}

footer > :first-child {
    font-size: 1.5rem;
    font-weight: 700;
}

/* main styling */

h1 img {
}

h1 {
    display: flex;
    justify-content: center;
    border-bottom: 1px solid black;
    background-color: rgba(0, 0, 0, 0.233);
    height: 100px;
}

button {
    padding: 8px 24px;
    border-radius: 999px;
    border: 1px solid black;
    outline: none;
    font-size: 1.5rem;
    background-color: rgba(255, 255, 255, 0.233);
}

button:hover {
    background-color: rgba(255, 255, 255, 0.575);
    margin-bottom: 2px;
}

.P2:hover,
.P1:hover {
    background-color: rgba(111, 193, 248, 0.61);
}
